"use client"

import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import Link from "next/link"

export function Navbar() {
  const { user, logout } = useAuth()
  const router = useRouter()

  const handleLogout = () => {
    logout()
    router.push("/login")
  }

  return (
    <nav className="bg-slate-900 text-white shadow-lg" role="navigation" aria-label="Main ">
      <div className="max-w-7xl mx-auto px-4 py-4 flex items-center justify-between">
        <div className="flex items-center gap-8">
          <Link href="/dashboard" className="font-bold text-lg hover:text-blue-400 transition">
            Petrol Station
          </Link>
        </div>

        <div className="flex items-center gap-4">
          <span className="text-sm text-slate-300">Welcome, {user?.name}</span>
          <Button variant="secondary" size="sm" onClick={handleLogout} aria-label="Logout">
            Logout
          </Button>
        </div>
      </div>
    </nav>
  )
}
