"use client"

import { useEffect, useState } from "react"
import { useAuth } from "@/context/auth-context"
import { useRouter } from "next/navigation"
import { ProtectedLayout } from "@/components/layout/protected-layout"
import { Card } from "@/components/ui/card"
import { Alert } from "@/components/ui/alert"
import StatCard from "@/components/dashboard/stat-card"
import { Volume2, DollarSign, Fuel, Clock } from "lucide-react"

export default function AttendantDashboardPage() {
  const { user, isAuthenticated, token } = useAuth()
  const router = useRouter()
  const [stats, setStats] = useState({
    totalSales: 0,
    fuelDispensed: 0,
    workingHours: 0,
    pendingTasks: 0,
  })
  const [error, setError] = useState("")

  useEffect(() => {
    if (!isAuthenticated || user?.role !== "attendant") {
      router.push("/login")
      return
    }

    const fetchDashboardData = async () => {
      try {
        const headers = { Authorization: `Bearer ${token}` }
        const [salesRes, fuelRes] = await Promise.all([
          fetch("http://localhost:5000/api/sales/attendant", { headers }),
          fetch("http://localhost:5000/api/fuel", { headers }),
        ])

        const salesData = await salesRes.json()
        const fuelData = await fuelRes.json()

        setStats({
          totalSales: salesData.total || 20,
          fuelDispensed: fuelData.totalDispensed || 500,
          workingHours: 8,
          pendingTasks: 3,
        })
      } catch {
        setError("‚ö†Ô∏è Unable to fetch data.")
      }
    }

    fetchDashboardData()
  }, [isAuthenticated, user, token, router])

  return (
    <ProtectedLayout>
      <div className="p-6 md:p-10 bg-slate-50 min-h-screen text-slate-900">
        <h1 className="text-3xl font-bold mb-6">Welcome, {user?.name}</h1>
        {error && <Alert variant="destructive">{error}</Alert>}

        {/* STAT CARDS */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <StatCard title="Total Sales" value={`${stats.totalSales}`} icon={<DollarSign />} color="bg-green-500" />
          <StatCard title="Fuel Dispensed (L)" value={`${stats.fuelDispensed}`} icon={<Fuel />} color="bg-blue-500" />
          <StatCard title="Working Hours" value={`${stats.workingHours} hrs`} icon={<Clock />} color="bg-orange-500" />
          <StatCard title="Pending Tasks" value={`${stats.pendingTasks}`} icon={<Volume2 />} color="bg-red-500" />
        </div>

        {/* ACCESSIBILITY AREA */}
        <div className="mt-10">
          <Card className="p-6">
            <h2 className="text-xl font-semibold mb-4">Accessibility Tools</h2>
            <div className="flex flex-wrap gap-4">
              <button
                className="bg-slate-800 text-white px-4 py-2 rounded-lg"
                onClick={() => {
                  const msg = new SpeechSynthesisUtterance("Fuel inventory running low!")
                  window.speechSynthesis.speak(msg)
                }}
              >
                üîä Audio Alert Example
              </button>

              <button
                className="bg-yellow-400 text-black px-4 py-2 rounded-lg font-semibold"
                aria-label="High Contrast Mode"
              >
                üåì Toggle High Contrast
              </button>
            </div>
          </Card>
        </div>
      </div>
    </ProtectedLayout>
  )
}
